import React from 'react';
import './App.css';
import { HashRouter as Router } from 'react-router-dom';
import Routes from './routes';
import { renderRoutes } from './routes/react-router-config' //cacherenderRouter
function App() {
  return (
    <Router>
        {renderRoutes(Routes)}
    </Router>
  );
}

export default App;
