import * as ActionTypes from '../../constants/charts';

const initialState = {
  // batch
  Dash_Financials: [],
  Dash_Attendance: [],
  Dash_TicketTypes: [],
  Dash_PricingTiers: [],
  Sell_ticket: [],
  Income_ticket: [],
  Income_ticket_dataset: [],
  Income_Cartesian: []
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    // batch
    case ActionTypes.SAVE_BATCHS:
      return { ...state, [action.set_name]: action.batchs };
    default:
      return state;
  }
}
